import React, { useState, useEffect } from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import { BrowserRouter as Router, Link, Route, Switch } from 'react-router-dom';
import firebase from './firebase'
import DetailsPage from './DetailsPage';
import './App.css'


const Data = () => {
    const [name, setName] = useState();
    const [surname, setSurname] = useState();
    //
    const [description, setDescription] = useState();
    const [age, setAge] = useState();
    const [location, setLocation] = useState();
    //
    const db = firebase.firestore();

    const [users, setUsers] = useState([]);

    const getName = (e) => {
        setName(e.target.value)
    }
    const getSurname = (e) => {
        setSurname(e.target.value)
    }
    //

    const getDescription = (e) => {
        setDescription(e.target.value)
    }
    const getLocation = (e) => {
        setLocation(e.target.value)
    }
    const getAge = (e) => {
        setAge(e.target.value)
    }


    const createUser = (e) => {
        e.preventDefault();
        db.collection('users').add({
            Name: name,
            Surname: surname,
            //
            Location: location,
            Description: description,
            Age: age
        })
            .then((res) => { console.log('users created') })
            .catch((err) => { console.log(err) })

    }

    useEffect(() => {

        let userInfo = [];
        db.collection('users').get()
            .then((res) => {
                res.forEach(action => {
                    userInfo.push({ ...action.data(), id: action.id });

                })
                setUsers(userInfo)

            })
    }, [])

    //delete user


    const deleteuser = (e) => {

        let uid = e.target.id
        db.collection('users').doc(uid).delete()
            .then(() => { console.log('user deleted') })
            .catch((err) => { console.log(err) })
    }
    //update user
    const updateuser = (e) => {
        let uid = e.target.id

        db.collection('users').doc(uid).update({
            Name: name,
            Surname: surname,
            Location: location,
            Description: description,
            Age: age
        })
            .then(() => { console.log('user updated') })
            .catch((err) => { console.log(err) })


    }
    ////di chance

    const [data, setData] = useState([])


    useEffect(() => {
        fetch('https://jsonplaceholder.typicode.com/posts')
            .then((res) => { return res.json() })
            .then((_data) => { setData(_data) })
            .catch((err) => { console.log(err) })
            .finally(() => { console.log('done') })

    }, [])



    return (
        <>
            <div className="container mt-4" >
                <form onSubmit={createUser}>
                    <div className="input-group">
                        <span className="input-group-text">First and last name</span>
                        <input Name="name" onChange={getName} type="text" aria-label="First name" className="form-control"></input>
                        <input Name="surname" onChange={getSurname} type="text" aria-label="Last name" className="form-control"></input>

                        <input Name="Description" onChange={getDescription} type="text" aria-label="Description" className="form-control"></input>
                        <input Name="Locattion" onChange={getLocation} type="text" aria-label="Locattion" className="form-control"></input>
                        <input Name="Age" onChange={getAge} type="text" aria-label="Age" className="form-control"></input>

                        <button type="submit" className="btn btn-primary">Add user</button>
                    </div>
                </form>


                {/*display from database*/}

                {
                    users.map((action) => [
                        <div className="card mt-4" key={action.id}>
                            <div className="card-header">
                                users
                            </div>
                            <div className="card-body">
                                <h5 className="card-title">User details</h5>
                                <Link to="./DetailsPage"><p className="card-text">{action.Name}, {action.Surname} </p></Link>

                                <button id={action.id} onClick={deleteuser} className="btn btn-primary">delete user</button> { }
                                <button id={action.id} onClick={updateuser} className="btn btn-primary">update user</button>


                            </div>
                        </div>

                    ])
                }
            </div>


            {
               


            }


        </>


    )


}
export default Data



